'use client'

import { useEffect, useState } from 'react'
import { useWallet } from '@/contexts/wallet-context'
import { getUserTokens, type RwaToken } from '@/lib/api'
import { Button } from '@/components/ui/button'
import { WalletButton } from '@/components/wallet/wallet-button'
import { Plus } from 'lucide-react'

// Placeholder pour la carte RWA qui sera créée dans la prochaine étape
const RwaCardPlaceholder = ({ tokenId }: { tokenId: number }) => (
  <div className="bg-neutral-800 border border-neutral-700 rounded-lg p-4 animate-pulse">
    <div className="w-full h-40 bg-neutral-700 rounded-md mb-4"></div>
    <div className="h-6 w-3/4 bg-neutral-700 rounded mb-2"></div>
    <div className="h-4 w-1/2 bg-neutral-700 rounded"></div>
  </div>
)


export default function DashboardPage() {
  const { isConnected, address, mounted, isCorrectNetwork } = useWallet()
  const [tokens, setTokens] = useState<RwaToken[]>([])
  const [isLoading, setIsLoading] = useState(false)
  const [error, setError] = useState<string | null>(null)

  useEffect(() => {
    if (mounted && isConnected && isCorrectNetwork && address) {
      const fetchTokens = async () => {
        setIsLoading(true)
        setError(null)
        try {
          const data = await getUserTokens(address)
          setTokens(data.tokens)
        } catch (err) {
          setError(err instanceof Error ? err.message : 'Une erreur est survenue.')
        } finally {
          setIsLoading(false)
        }
      }
      fetchTokens()
    } else if (mounted && isConnected && !isCorrectNetwork) {
        setError("Veuillez vous connecter au réseau Chiliz Spicy Testnet.")
        setTokens([])
    }
    else {
      setTokens([]) // Reset tokens when disconnected
    }
  }, [mounted, isConnected, isCorrectNetwork, address])

  const renderContent = () => {
    if (!mounted) {
        // Affiche des placeholders pendant l'hydratation côté client pour éviter le layout shift
        return (
            <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-6">
              {[...Array(4)].map((_, i) => <RwaCardPlaceholder key={i} tokenId={i} />)}
            </div>
        )
    }

    if (!isConnected) {
      return (
        <div className="text-center py-20">
          <h2 className="text-2xl font-semibold mb-4">Connectez votre portefeuille</h2>
          <p className="text-neutral-400 mb-6">Pour voir et gérer vos actifs numériques, connectez votre portefeuille Chiliz.</p>
          <WalletButton />
        </div>
      )
    }

    if (error) {
        return <div className="text-center py-20 text-red-400">{error}</div>
    }

    if (isLoading) {
      return (
        <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-6">
          {[...Array(tokens.length || 4)].map((_, i) => <RwaCardPlaceholder key={i} tokenId={i} />)}
        </div>
      )
    }
    
    if (tokens.length === 0) {
      return (
        <div className="text-center py-20 border-2 border-dashed border-neutral-700 rounded-lg">
          <h2 className="text-2xl font-semibold mb-4">Aucun actif numérique trouvé</h2>
          <p className="text-neutral-400 mb-6">Commencez votre collection en tokenisant votre premier actif.</p>
          <Button>
            <Plus className="mr-2 h-4 w-4" />
            Tokeniser un actif
          </Button>
        </div>
      )
    }

    return (
      <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-6">
        {/*
          Ici, nous allons bientôt remplacer ce div par le vrai composant RwaCard
        */}
        {tokens.map((token) => (
          <div key={token.tokenId} className="bg-neutral-800 border border-neutral-700 rounded-lg p-4">
             <div className="w-full h-40 bg-neutral-700 rounded-md mb-4 flex items-center justify-center">
                <span className="text-neutral-500">Image</span>
             </div>
             <h3 className="font-bold text-lg">{token.rwaType} #{token.tokenId}</h3>
             <p className="text-sm text-neutral-400">{token.percent}%</p>
          </div>
        ))}
      </div>
    )
  }

  return (
    <div className="container mx-auto px-4 sm:px-6 lg:px-8 py-8">
        <header className="flex flex-col sm:flex-row items-start sm:items-center justify-between mb-8">
            <div>
                <h1 className="text-3xl font-bold tracking-tight text-white">Mon Portefeuille</h1>
                <p className="mt-1 text-sm text-neutral-400">
                    {isConnected ? `Connecté avec ${address?.substring(0, 6)}...${address?.substring(address.length - 4)}` : "Parcourez et gérez vos actifs numériques sur Chiliz."}
                </p>
            </div>
            {isConnected && (
                <Button className="mt-4 sm:mt-0">
                    <Plus className="mr-2 h-4 w-4" />
                    Tokeniser un actif
                </Button>
            )}
      </header>
      <main>
        {renderContent()}
      </main>
    </div>
  )
}
